import * as React from 'react';
import Stacking from '../model/Stacking';
import Placement from '../model/Placement';
import createPlotlyComponent from 'react-plotly.js/factory';
var Plot = createPlotlyComponent(Plotly);

function getGlobalConfig() {
  return {
    displayModeBar: false,
    responsive: true
  };
}

function getCommonLayout() {
  return {
    autosize: true,
    title: false,
    margin: {
      l: 60,
      t: 20,
      b: 60
    }
  };
}

function getHiddenAxisConfig() {
  return {
    zeroline: false,
    showticklabels: false,
    showgrid: false
  };
}

export function PlotlyLineChart(props) {
  var chartData = props.data;
  var plotData = [];
  chartData.series.forEach(function (s) {
    var categories = chartData.categories;

    if (categories.length === 0) {
      s.yValues.forEach(function (v, i) {
        categories.push(String(i));
      });
    }

    var trace = {
      type: "scatter",
      mode: "lines+points",
      name: s.name,
      x: categories,
      y: s.yValues,
      line: {
        shape: "spline"
      }
    };

    if (chartData.yAxis.stacking === Stacking.ABSOLUTE) {
      trace.stackgroup = "one";
    }

    plotData.push(trace);
  });
  var plotConfig = Object.assign({}, getGlobalConfig());
  var layout = Object.assign({}, getCommonLayout(), {
    autosize: true,
    xaxis: {
      title: chartData.xAxis.title
    },
    yaxis: {
      title: chartData.yAxis.title
    }
  });
  return React.createElement(Plot, {
    className: props.className,
    style: props.style,
    data: plotData,
    config: plotConfig,
    layout: layout,
    useResizeHandler: true
  });
}
export function PlotlyBarChart(props) {
  var chartData = props.data;
  var plotData = [];
  var isHorizontal = chartData.yAxis.placement === Placement.BOTTOM;
  chartData.series.forEach(function (s) {
    var categories = chartData.categories;

    if (categories.length === 0) {
      s.yValues.forEach(function (v, i) {
        categories.push(String(i));
      });
    }

    var trace = {
      type: "bar",
      name: s.name,
      x: categories,
      y: s.yValues,
      orientation: "v"
    };

    if (isHorizontal) {
      trace.x = s.yValues;
      trace.y = categories;
      trace.orientation = "h";
    }

    plotData.push(trace);
  });
  var plotConfig = Object.assign({}, getGlobalConfig());
  var layout = Object.assign({}, getCommonLayout(), {
    autosize: true,
    xaxis: {
      title: isHorizontal ? chartData.yAxis.title : chartData.xAxis.title
    },
    yaxis: {
      title: isHorizontal ? chartData.xAxis.title : chartData.yAxis.title
    }
  });

  if (chartData.yAxis.stacking === Stacking.ABSOLUTE) {
    layout.barmode = "stack";
  }

  return React.createElement(Plot, {
    className: props.className,
    style: props.style,
    data: plotData,
    config: plotConfig,
    layout: layout,
    useResizeHandler: true
  });
}

function _internalPlotlyPieChart(props, hole) {
  var chartData = props.data;
  var plotData = [];
  var pieCount = chartData.series.length;
  var columns = Math.min(pieCount, 4);
  var rows = Math.ceil(pieCount / columns);
  chartData.series.forEach(function (s, i) {
    var categories = chartData.categories;

    if (categories.length === 0) {
      s.yValues.forEach(function (v, i) {
        categories.push(String(i));
      });
    }

    var trace = {
      type: "pie",
      name: s.name,
      labels: categories,
      values: s.yValues,
      domain: {
        rows: Math.floor(i / columns),
        column: i % columns
      }
    };

    if (typeof hole !== "undefined") {
      trace.hole = hole;
    }

    plotData.push(trace);
  });
  var plotConfig = Object.assign({}, getGlobalConfig());
  var layout = Object.assign({}, getCommonLayout(), {
    autosize: true,
    grid: {
      rows: rows,
      columns: columns
    },
    xaxis: getHiddenAxisConfig(),
    yaxis: getHiddenAxisConfig()
  });
  layout.margin.b = 20;
  return React.createElement(Plot, {
    className: props.className,
    style: props.style,
    data: plotData,
    config: plotConfig,
    layout: layout,
    useResizeHandler: true
  });
}

export function PlotlyPieChart(props) {
  return _internalPlotlyPieChart(props);
}
export function PlotlyDoughnutChart(props) {
  return _internalPlotlyPieChart(props, .4);
}
export function PlotlyRadarChart(props) {
  var chartData = props.data;
  var plotData = [];
  chartData.series.forEach(function (s) {
    var categories = chartData.categories;

    if (categories.length === 0) {
      s.yValues.forEach(function (v, i) {
        categories.push(String(i));
      });
    }

    var trace = {
      type: "scatterpolar",
      name: s.name,
      theta: categories,
      r: s.yValues,
      fill: "toself"
    };
    plotData.push(trace);
  });
  var plotConfig = Object.assign({}, getGlobalConfig());
  var layout = Object.assign({}, getCommonLayout(), {
    autosize: true,
    polar: {
      radialaxis: {
        visible: true
      }
    },
    xaxis: getHiddenAxisConfig(),
    yaxis: getHiddenAxisConfig()
  });
  layout.margin.b = 20;
  return React.createElement(Plot, {
    className: props.className,
    style: props.style,
    data: plotData,
    config: plotConfig,
    layout: layout,
    useResizeHandler: true
  });
}
